<template>
  <div>
    <!-- <active-user :username="user.name" :userage="user.age"></active-user> -->
    <header><h1>User Data</h1></header>
    <ul>
      <active-user
        v-for="user in users"
        :key="user.name"
        :username="user.name"
        :userage="user.age"
      ></active-user>
    </ul>
    <user-data @set-data="setUserData"></user-data>
  </div>
</template>

<script>
export default {
  data() {
    return {
      users: [
        {
          name: "Noor Poonia",
          age: 19,
        },
      ],
    };
  },
  methods: {
    setUserData(name, age) {
      const newUser = {
        name: name,
        age: age,
      };
      this.users.push(newUser);
      // this.user = {
      //   name: name,
      //   age: +age,
    },
  },
};
</script>

